:root {

  /* FONTS */
  --headerFont: 'Maitree', serif;
  --bodyFont: 'Montserrat', sans-serif;

  /* COLOUR */
  --lightBlue: #5da9c2;
  --darkBlue: #261870;
  --turquoise: #ace8cd;
  --errRed: #c00000;
  --okGreen:  #049904;
}

body {
  font-family: var(--bodyFont);
  position: relative;
  min-height: 100vh;
  padding-top: 30px;
}

h1, h3 {
  font-family: var(--headerFont);
}

.wrapper {
  max-width: 940px;
  margin: 0 auto;
  width: 90%;
  padding-bottom: 50px;
}

header {
  margin:0 20px;
}

h1 {
 font-size: 2rem;
 text-align: center;
 margin-bottom: 100px;
}

.flex {
  display:flex;
  justify-content: space-between;
}
 
footer {
  position: absolute;
  bottom: 0;
  height: 50px;
  width: 100%;
}

footer p {
  background-color:lightBlue;
  padding: 7px;
  text-align: center;
}

footer p a:hover {
  color: var(--darkBlue);
}